import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ProfileComponent } from './profile.component';

const routes: Routes = [
  { path: '', component: ProfileComponent },
  { path: 'quiz', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\quiz\\quiz.module')['QuizModule']);  });}) },
  { path: 'history', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\result-history\\result-history.module')['ResultHistoryModule']);  });}) }
];

export const routing: ModuleWithProviders = RouterModule.forChild(routes);




